import React, {Component} from 'react';
import TestSwiper from "./TestSwiper";

class SwiperContainer extends Component {
    constructor(props) {
        super(props);
        this.state ={
            count: 0
        }
    }

    onTouchMove = () =>{
        console.log("TOUCHED");
        let countt = this.state.count;
        this.setState({count: countt + 1});
    };
    render() {
        return (
            <div>
                <br/>
                <div >{this.state.count}</div>
                <TestSwiper imgLink={"https://sun9-17.userapi.com/c841322/v841322634/36d69/IrmwPwh6tlI.jpg"}
                    onTouchMove={this.onTouchMove}/>

            </div>
        );
    }
}

export default SwiperContainer;
